<template>
  <v-card
    flat
    color="var(--v-surface-base)"
  >
    <v-card
      flat
      color="var(--v-surface-base)"
    >
      <v-card-actions>
         <v-tooltip bottom>
          <template #activator="{ on, attrs }">
            <v-btn
              icon
              class="mx-2"
              min-width="0"
              :input-value="!$vuetify.theme.dark"
              v-bind="attrs"
              v-on="on"
              @click="$vuetify.theme.dark = false"
            >
              <v-icon>{{ mdiWhiteBalanceSunny }}</v-icon>
            </v-btn>
          </template>
          <span>明亮模式</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template #activator="{ on, attrs }">
            <v-btn
              icon
              class="mx-2"
              min-width="0"
              :input-value="$vuetify.theme.dark"
              v-bind="attrs"
              v-on="on"
              @click="$vuetify.theme.dark = true"
            >
              <v-icon>{{ mdiWeatherNight }}</v-icon>
            </v-btn>
          </template>
          <span>深色模式</span>
        </v-tooltip>
      </v-card-actions>
    </v-card>
  </v-card>
</template>

<script>
import { mdiWhiteBalanceSunny, mdiWeatherNight } from '@mdi/js'

export default {
  name: 'ThemeSwitcher',

  data: () => ({
    mdiWhiteBalanceSunny,
    mdiWeatherNight
  }),

  mounted () {
    if(window?.matchMedia('(prefers-color-scheme: dark)')?.matches) {
      this.$vuetify.theme.dark = true 
    }
    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', this.changeTheme, false)
  },

  beforeDestroy () {
    window.matchMedia('(prefers-color-scheme: dark)').removeEventListener('change', this.changeTheme)
  },

  methods: {
    changeTheme(evt) {
      this.$vuetify.theme.dark = evt.matches ? true : false
    }
  }
}
</script>