<template>
  <v-app>
    <v-main class="d-flex justify-center align-start align-lg-center align-xl-center align-md-center">
      <v-container
        fluid
        class="pa-0"
      >
        <v-row no-gutters>
          <v-col class="d-flex justify-center align-center">
            <transition name="slide-x-reverse-transition">
              <v-card
                v-show="onMounted"
                class="d-flex flex-column"
                color="var(--v-surface-base)"
                elevation="0"
                :rounded="isMobile ? '': 'xl'"
                :height="surfaceHeight"
                :max-height="surfaceHeight"
                :width="surfaceWidth"
              >
                <v-card-actions class="justify-end pb-0">
                  <theme-switcher></theme-switcher>
                </v-card-actions>
                <v-card-text class="overflow-y-auto py-0">
                  <v-container
                    fluid
                    class="fill-height"
                  >
                    <v-row
                      no-gutters
                      class="fill-height"
                    >
                      <v-col class="fill-height">
                        <the-heading></the-heading>
                        <skills></skills>
                        <experience></experience>
                        <education></education>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-spacer></v-spacer>
                <v-card-actions class="justify-center py-0">
                  <v-btn icon>
                    <v-icon x-large>{{ mdiChevronDown }}</v-icon>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </transition>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { mdiChevronDown } from '@mdi/js'
import ThemeSwitcher from './components/ThemeSwitcher.vue'
import TheHeading from './components/TheHeading.vue'
import Skills from './components/Skills.vue'
import Experience from './components/Experience.vue'
import Education from './components/Education.vue'

export default {
  name: 'App',

  components: {
    ThemeSwitcher,
    TheHeading,
    Skills,
    Experience,
    Education
  },

  data: () => ({
    mdiChevronDown,
    onMounted: false,
  }),

  computed: {
    isMobile() {
      switch(this.$vuetify.breakpoint.name) {
        case 'xs': return true
        case 'sm': return true
        default: return false
      }
    },

    surfaceHeight() {
      const height = window.innerHeight
      const padding = this.isMobile ? 0 : 48
      return `${height - padding}px`
    },

    surfaceWidth() {
      const width = window.innerWidth
      const padding = this.isMobile ? 0 : 48
      return `${width - padding}px`
    }
  },

  mounted() {
    this.onMounted = true
  }
};
</script>
